import { v4 as uuid } from 'uuid'
import dayjs from 'dayjs'
class TeamDB {
  constructor() {
    this.collection = 'team'
    this.data = JSON.parse(localStorage.getItem('team')) || []
  }
  getAll() {
    return this.data
  }
  getByUser(userId) {
    return this.data.filter(x => x.userId === userId)
  }

  get(id) {
    try {
      const result = this.data.filter(x => x.id === id)
      if (result.length !== 0) return result[0]
      else return null
    } catch (err) {
      console.error(`Get data ${id} failed, because ${err}`)
      throw err
    }
  }

  getByExtraId(id) {
    try {
      const result = this.data.filter(x => x.extraId === id)
      if (result.length !== 0) return result
      else return null
    } catch (err) {
      console.error(`Get data ${id} failed, because ${err}`)
      throw err
    }
  }

  updateTeam(origin, target) {
    let keys = Object.keys(origin)
    for (let key of keys) {
      origin[key] = target[key]
    }
    origin['time_update'] = dayjs().format('YYYY/MM/DD HH:mm:ss')
    origin['status'] = 'PENDING'
  }

  createTeam(data) {
    let temp = data
    temp['id'] = uuid()
    temp['time_create'] = dayjs().format('YYYY/MM/DD HH:mm:ss')
    this.data.push(temp)
  }

  createOrUpdate(data) {
    const result = this.get(data.id)
    if (result) this.updateTeam(result, data)
    else this.createTeam(data)

    this.saveToStore()
  }

  getAmount(userId) {
    const items = this.data
    let temp = {}
    items.forEach(item => {
      if (item.user.id !== userId) {
        if (typeof temp[item['extraId']] == 'undefined') temp[item['extraId']] = 1
        else temp[item['extraId']]++
      }
    })
    return temp
  }

  saveToStore() {
    localStorage.removeItem('team')
    localStorage.setItem('team', JSON.stringify(this.data))
  }
}

export default new TeamDB()
